import React, { useState } from 'react';
import styled from '@emotion/styled';
import { availableLocations } from './../utils/helpers';
import LocationMark from './../Components/LocationMark';
import ThemeSwitch from './../Components/ThemeSwitch';
const WeatherSettingWrapper = styled.div`
  box-sizing: border-box;
  ${'' /* position: relative; */}
  min-width: 360px;
  height: 360px;
  box-shadow: ${({ theme }) => theme.boxShadow};
  &:hover {
    box-shadow: ${({ theme }) => theme.hoverBoxShadow};
    transition: 0.2s;
  }
  transition: 0.2s;
  background-color: ${({ theme }) => theme.foregroundColor};
  box-sizing: border-box;
  padding: 30px 30px;
  border-radius: 25px;
`;

const Title = styled.div`
  font-size: 35px;
  color: ${({ theme }) => theme.titleColor};
  margin-bottom: 70px;
`;

const StyledSelect = styled.select`
  display: block;
  box-sizing: border-box;
  background: transparent;
  border: 1px solid ${({ theme }) => theme.textColor};
  border-radius: 2px;
  outline: none;
  width: 100%;
  max-width: 100%;
  color: ${({ theme }) => theme.textColor};
  font-size: 16px;
  padding: 7px 10px;
  margin-bottom: 40px;
  -webkit-appearance: none;
  -moz-appearance: none;
  box-shadow: none;
  outline: 0;
  cursor: pointer;
`;

const ButtonGroup = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  > button {
    display: flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
    user-select: none;
    margin: 0;
    letter-spacing: 0.3px;
    line-height: 1;
    cursor: pointer;
    overflow: visible;
    text-transform: none;
    border: 1px solid transparent;
    background-color: transparent;
    height: 35px;
    width: 80px;
    border-radius: 5px;
    font-size: 14px;
    &:focus,
    &.focus {
      outline: 0;
      box-shadow: none;
    }
    &::-moz-focus-inner {
      padding: 0;
      border-style: none;
    }
  }
`;

const Back = styled.button`
  && {
    color: ${({ theme }) => theme.textColor};
    border-color: ${({ theme }) => theme.textColor};
  }
  &:hover {
    background-color: ${({ theme }) => theme.buttonHoverBackgroundColor};
  }
`;

const Save = styled.button`
  && {
    color: white;
    background-color: #40a9f3;
  }
  &:hover {
    background-color: #2387cf;
  }
`;

const WeatherSetting = (props) => {
  const {
    handleCurrentCityChange,
    handleSettingPageChange,
    cityName,
    handleThemeSwitch,
  } = props;
  const [locationName, setLocationName] = useState(cityName);

  const handleChange = (e) => {
    // console.log('handleChange',e.target.value);
    setLocationName(e.target.value);
  };

  const handleSave = () => {
    handleCurrentCityChange(locationName);
    handleSettingPageChange('WeatherCards');
    localStorage.setItem('cityName', locationName);
  };

  return (
    <WeatherSettingWrapper>
      <LocationMark
        handleSettingPageChange={() => handleSettingPageChange('WeatherCards')}
      />

      <ThemeSwitch handleThemeSwitch={handleThemeSwitch} />
      <Title>選地區</Title>
      <StyledSelect
        id="location"
        name="location"
        onChange={handleChange}
        value={locationName}
      >
        {availableLocations.map(({ cityName }) => (
          <option value={cityName} key={cityName}>
            {cityName}
          </option>
        ))}
      </StyledSelect>

      <ButtonGroup>
        <Back onClick={() => handleSettingPageChange('WeatherCards')}>
          返回
        </Back>
        <Save onClick={handleSave}>儲存</Save>
      </ButtonGroup>
    </WeatherSettingWrapper>
  );
};

export default WeatherSetting;
